import React from 'react';
import Op from './OldPosts.module.css';

const PostUser = (props) => {
    return (
        <div className={Op.posts}>{props.id} {props.message} {props.LikesCount}
        </div>
    );
}

const OldPosts = (props) => {

let PostsEl = props.post.dataOldPosts
    .map(post => <PostUser id={post.id} message={post.message} LikesCount={post.LikesCount}/>);
    let newPostsEl = props.post.newPosts
    .map((post) => <PostUser id={post.id} message={post.message} LikesCount={post.LikesCount}/>);
    return (
        <div className={Op.posts}>
            <div className={Op.item}>

                <div>
                    <img src="https://i.pinimg.com/originals/33/b8/69/33b869f90619e81763dbf1fccc896d8d.jpg"
                         alt=""/>{PostsEl }{newPostsEl}

                </div>

            </div>
        </div>

        );
    }

export default OldPosts;