import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import App from './App'; // принимаем App
import  {addPosts} from './Data/Data'
import {addNewMessage} from "./Data/Data";
import {BrowserRouter} from "react-router-dom";



export let rerenderAll = (Data) => {
    ReactDOM.render(
        <React.StrictMode>
            <BrowserRouter>
                <App dialogs={Data.dialogs} addNewMessage={addNewMessage}  post={Data.post} addPosts={addPosts}/>
            </BrowserRouter>
        </React.StrictMode>,
        document.getElementById('root')
    );
}



