import React from 'react';
import './App.css';
import Profile from './components/Profile/Profile';
import Header from './components/Header/Header';
import Nav from './components/Nav/Nav';
import Dialogs from "./components/Dialogs/Dialogs.jsx";
import {BrowserRouter, Route} from 'react-router-dom';
import News from "./components/News/News";
import Music from "./components/Music/Music";
import Settings from "./components/Settings/Settings";
import Friends from "./components/Friends/Friends";


const App = (props) => {

    return (
        <BrowserRouter>
            <div className="app-wrapper">
                <Header/>
                <Nav/>
                <div className='app-wrapper-content'>
                    <Route path='/dialogs' render={ () => <Dialogs dialogs = {props.dialogs} addNewMessage={props.addNewMessage}/>}/>
                    <Route path='/profile' render={ () => <Profile post={props.post} addPosts={props.addPosts}/>}/>
                    <Route path='/News' render={ () => <News />}/>
                    <Route path='/Music' render={ () => <Music />}/>
                    <Route path='/Settings' render={ () => <Settings />}/>
                    <Route path='/Friends' render={ () => <Friends />}/>
                </div>

            </div>
        </BrowserRouter>
    );

}
export default App; // экспортируем Арр по дефолту можно только одну функцию
