import React from 'react';
import d from './DialogsUser.module.css';
import {NavLink} from "react-router-dom";

const DialogUser = (props) => {
        let path = '/dialogs/' + props.id;
    return <div className={d.dialog} >
        <NavLink to={path} activeClassName={d.active}>{props.name}</NavLink>
    </div>
}
const DialogsUser = (props) => {
    let dialogsEl = props.dataDialogs
        .map((dial) => <DialogUser id={dial.id} name={dial.name}/>);

    return (
        <div className={d.dialogs}>
            <div className={d.dialogs__items}>
                {dialogsEl}
            </div>
        </div>

    );
}


export default DialogsUser;