import React from 'react';
import Np from './NewPosts.module.css';

const NewPosts = (props) => {
    let newPostEl = React.createRef();/*реакт создай ссылку для переменной newPostEl */
    let addPosts = () => {
        let addP = newPostEl.current.value;/* addP принимает newPostEl.текущее.значение; */
        /*let addP= document.getElementById('new-post').value; alert (addP) можно так но так не делаем
        * так как это прямое обращение к DOM мы же  работаем с Virtual DOM*/
        props.addPosts(addP);
       // console.log(addP);
        newPostEl.current.value = '';
    }
    return (
        <div className={Np.newposts}>

            <div className={Np.posts}>
                My posts
                <div className={Np.item}>new posts</div>
            </div>
            <div>
            <textarea ref={newPostEl}>Comments</textarea>{/*привязываем ссылку к textarea*/}
            </div>
            <div>
            <button onClick={ addPosts }>Add Posts</button>
            </div>
        </div>
    );
}

export default NewPosts;