import React from 'react';
import n from './Nav.module.css';
import {NavLink} from "react-router-dom";


const Nav = () => {
    return <nav className={n.nav}>
        <div className={n.nav__areas}>
            <div className={n.areas__item}>
                <div className={n.item}>
                    {' '}
                    <NavLink to='/Profile' activeClassName={n.active}>Profile</NavLink>
                </div>
                <div className={n.item}>
                    {' '}
                    <NavLink to='/Dialogs' activeClassName={n.active}>Messages</NavLink>
                </div>
                <div className={n.item}>
                    {' '}
                    <NavLink to='/News' activeClassName={n.active}>News</NavLink>
                </div>
                <div className={n.item}>
                    {' '}
                    <NavLink to='Music' activeClassName={n.active}>Music</NavLink>
                </div>
                <div className={n.item}>
                    {' '}
                    <NavLink to='Settings' activeClassName={n.active}>Settings</NavLink>
                </div>
            </div>
            <div className={n.areas__friends}>
                <div className={n.friends}>
                    <NavLink to='Friends' activeClassName={n.active}>Friends</NavLink>
                    <div className={n.friend_name}>
                        <NavLink to='Friends' activeClassName={n.active_name}>Vasia</NavLink>
                        <NavLink to='Friends' activeClassName={n.active_name}>Max</NavLink>
                        <NavLink to='Friends' activeClassName={n.active_name}>Vika</NavLink>
                        <NavLink to='Friends' activeClassName={n.active_name}>Nina</NavLink>
                    </div>
                </div>
            </div>
        </div>
    </nav>
}

export default Nav;