import React from 'react';
import d from './Dialogs.module.css';
import Message from "./MessageUser/MessageUser";
import DialogsUser from "./DialogsUser/DialogsUser";




const Dialogs = (props) => {
    return (
        <div className={d.dialogs}>
            <div className={d.dialogs__items}>

                <DialogsUser dataDialogs={props.dialogs.dataDialogs}/>
            </div>
            <div className={d.messages}>

                <Message dataMessage={props.dialogs.dataMessage} addNewMessage={props.addNewMessage}/>

                </div>
        </div>
       );
}


export default Dialogs;