import React from 'react';
import d from './MessageUser.module.css';

const MessageUser = (props) => {
    return <div className={d.message}>{props.id} {props.message}</div>
  }


const Message = (props) => {
    //
    let messagesEl = props.dataMessage
        .map((mes) => <MessageUser id={mes.id} message={mes.message}/>);

    let newMessageEl = React.createRef();
    let addMessage = () => {

        let addM = newMessageEl.current.value;
        props.addNewMessage(addM);/* addM принимает newMessageEl.текущее.значение; */
        /*let addP= document.getElementById('new-post').value; alert (addP) можно так но так не делаем
        * так как это прямое обращение к DOM мы же  работаем с Virtual DOM*/
        /*реакт создай ссылку для переменной newPostEl */

        //
        //console.log(addM);
        // alert("\u{1F60D}");
        newMessageEl.current.value = "";//зануляем строку в форме ввода
    }



    return (
        <div className={d.dialogs}>
            <div className={d.messages}>
                {messagesEl}
                <div spellCheck='true'>
                    <textarea ref={newMessageEl}>сообщение</textarea>
                </div>
                <div>
                    <button onClick={addMessage}>отправить</button>
            </div>
        </div>
            </div>

    )
}

export default Message;