import React from 'react';
import h from './Header.module.css';

const Header = () => {
    return <header className={h.header}>
    <img className={h.img}
      src="https://png.pngtree.com/png-clipart/20191121/original/pngtree-travel-logo-holidays-tourism-business-trip-company-logo-design-vector-illustration-png-image_5102346.jpg"
      alt="logo"
    />
  </header>;
}
export default Header;